@font-face {
  font-family: 'KMR-b';
  src: url('./fonts/한국기계연구원_bold.ttf') format('woff');
}
@font-face {
  font-family: 'KMR';
  src: url('./fonts/한국기계연구원_Light.ttf') format('woff');
}
@font-face {
  font-family: 'Akira';
  src: url('./fonts/Akira\ Expanded\ Demo.otf') format('woff');
}
@font-face {
  font-family: 'prten-l';
  src: url('./fonts/Pretendard-Light.otf') format('woff');
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: 'Racing Sans One', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #000000;
  color: #FFFFFF;
}

.navbar {
  background-color: #000000;
  height: calc(100vh - 100px); /* 전체 화면 높이에서 100px을 뺀 크기로 설정 */
  color: white;
  display: flex;
  align-items: center;
  padding-left: 10px;
  flex-wrap: wrap; /* 내용이 너무 많을 경우 줄바꿈 */
  justify-content: space-between;
}


.navbar a {
  color: white;
  text-decoration: none;
}

.navbar h1 {
  margin-right: auto;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.video-container {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  margin-top: 400px; /* navbar의 높이와 동일하게 설정 */
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


.button {
  background-color: #004FA7;
  color: white;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
}

.logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* 화면 높이 전체를 차지하도록 설정 */
  width: 100vw; /* 화면 너비 전체를 차지하도록 설정 */
}

.logo-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain; /* 이미지 비율 유지 */
}

.album-cover-slider {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px; /* 원하는 상단 여백 크기로 설정 */
}

.album-cover-slider img {
  max-width: 50%;
  height: auto;
}

.slider-controls {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.slider-controls button {
  font-family: 'Days One', sans-serif;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  background-color: #000;
  color: #fff;
  font-size: 16px;
  transition: background-color 0.3s;
}

.slider-controls button:hover {
  background-color: #333;
}

.credits {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  font-size: 36px; /* 타이틀 크기 */
  margin: 100px; /* 타이틀과 크레딧 사이의 간격 */
}
.songCredit,
.musicVideoCredit {
  margin-bottom: 40px; /* 각 크레딧 섹션 사이의 간격 */
}
.songCredit h2,
.musicVideoCredit h2 {
  font-size: 24px; /* 크레딧 제목 크기 */
  margin-bottom: 10px; /* 각 크레딧 섹션 사이의 간격 */
}

.credit-category {
  color: #555; /* 제작 부분 색상 */
}

.credit-names {
  color: #E9EEF1; /* 제작 담당자 색상 */
}
.credit-container {
  max-width: fit-content; /* 타이틀 텍스트를 넘어가지 않게 */
  margin: 0 auto; /* 가운데 정렬 */
  text-align: left; /* 왼쪽 정렬 */
  margin: 0 71px;
}
.title,
.songCredit h2,
.musicVideoCredit h2,
.credit-category,
.credit-names {
  font-family: 'Akira', sans-serif;
}
.goods-button {
  /* 굿즈 신청 버튼 스타일 */
  background-color: #E9EEF1;
  color: #213551;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  font-family: 'KMR-b', sans-serif;
  margin: 5px;
  transition: background-color 0.3s;
}
.goods-button:hover,
.contact-button:hover {
  background-color: #0056b3;
}
.goods-info {
  /* 굿즈 정보 스타일 */
  background-color: #E9EEF1;
  color: #213551;
  padding: 20px;
  border: 1px solid #ddd;
}
.goods-image {
  width: 100%;
  max-width: 300px;
  height: auto;
  margin-bottom: 10px;
}
.purchase-button {
  background-color: #007aff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  width: 100%;
  text-align: center;
  font-weight: bold;
}
.purchase-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  position: relative; /* 자식 요소인 .close-button의 위치를 조정하기 위해 */
  text-align: center; /* 내부 컨텐츠를 가운데 정렬 */
  background-color: #fff;
  color: #000000;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: 'prten-l', sans-serif;
  max-width: 400px; /* 최대 너비를 400px로 제한 */
  overflow-x: hidden; /* 좌우 스크롤 방지 */
  width: 100%; /* 컨테이너의 너비를 화면 너비와 동일하게 설정 */
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* App.css */
.album-credit-button {
  margin-top: 50px;
  font-family: 'Akira', sans-serif;
  font-size: 16px;
  background-color: #f0f0f0;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  max-width: 65%;
  width: 50%;
}

.album-credit-button:hover {
  background-color: #DCFDFE;
}

/* 데스크탑 화면 크기에 따른 스타일 */
@media (min-width: 768px) {
  .modal-content {
    width: auto; /* 데스크탑 화면에서는 자동 너비 */
  }
}
.contact-button {
  /* Contact 버튼 스타일 */
  background-color: #E9EEF1;
  color: #000000;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  font-family: 'KMR-b', sans-serif;
  margin: 5px;
  transition: background-color 0.3s;
}

.contact-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #fff;
  border-radius: 10px;
  width: 80%;
  max-width: 500px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: auto;
}
.goods-modal,
.contact-modal {
  z-index: 9999; /* 다른 요소보다 높은 z-index 값을 지정 */
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}


.modal-header {
  position: absolute; /* 부모 요소인 .modal-content 내에서의 위치를 절대값으로 설정 */
  right: 0; /* 우측 상단에 위치 */
}

.close-button {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  margin-right: 18px;
}
.goods-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  text-align: center;
}

.goods-item img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}

.goods-item p {
  margin: 5px 0;
  font-size: 14px;
}


@media screen and (max-width: 768px) { /* 모바일 화면 크기에 대한 반응형 디자인 */
  .navbar {
    height: auto; /* 높이를 자동으로 설정 */
    padding: 20px 10px; /* 패딩을 조금 더 추가 */
  }
  .goods-modal {
    padding: 10px;
    margin: 20px 0;
  }
  .goods-item {
    width: 95%;
  }
  .close-button {
    font-size: 20px;
  }
  .modal-content {
    padding: 10px;
    width: 90%; /* 화면 너비 전체를 차지하도록 설정 */ /* 화면 너비 전체를 차지하도록 설정 */
  }
  .credit-container {
    font-size: 14px; /* 원하는 텍스트 크기로 조정 */
  }
  .contact-modal{
    margin: 20px 0;
  }
  .video-container{
    margin-top: 100px;
  }
}